<template>
  <div>
    <v-app-bar app color="white" class="elevation-0 blur">
      <v-icon @click="pageBack">mdi-arrow-left</v-icon>
      <v-spacer></v-spacer>
      <span class="headline">{{ role=="teacher" ? "學生列表": "小孩列表"}}</span>
      <v-spacer></v-spacer>
      <v-icon @click="addSheet = true">mdi-plus</v-icon>
      <div class="mx-2"></div>
      <v-icon @click="deleteSelected">mdi-delete</v-icon>
    </v-app-bar>
    <div class="d-flex justify-center flex-wrap pa-2">
      <div class="d-flex flex-wrap" style="flex: 0 0 100%">
        <v-card class="ma-1" v-for="(classmate,index) in classmates" :key="index">
          <v-avatar
            @click="changeStatus(classmate)"
            :color="classmate.selected?'primary':'grey'"
            :size="90"
          >
            <v-img src="@/assets/pic/baby.png"></v-img>
          </v-avatar>
          <div>{{ classmate.name }}</div>
        </v-card>
      </div>
    </div>
    <v-bottom-sheet v-model="addSheet">
      <v-sheet class="pa-4">
        <v-text-field v-model="newStudentName" label="學生姓名"></v-text-field>
        <v-btn block color="primary" @click="addStudent">添加</v-btn>
      </v-sheet>
    </v-bottom-sheet>
  </div>
</template>

<script>
export default {
  computed: {
    role() {
      return this.$store.getters.role;
    },
  },
  data() {
    return {
      classmates: Array.from(Array(20)).map((n, i) => {
        return {
          name: "學生" + i,
          selected: false,
        };
      }),
      addSheet: false,
      newStudentName: "",
    };
  },
  methods: {
    pageBack() {
      this.$router.go(-1);
    },
    changeStatus(classmate) {
      classmate.selected = !classmate.selected;
    },
    deleteSelected() {
      this.classmates = this.classmates.filter((c) => c.selected == false);
    },
    addStudent() {
      if (!this.newStudentName) return;
      this.classmates.unshift({ name: this.newStudentName, selected: false });
      this.newStudentName = "";
      this.addSheet = false;
    },
  },
};
</script>

<style scoped>
.blur {
  -webkit-backdrop-filter: saturate(180%) blur(10px) !important;
  backdrop-filter: saturate(180%) blur(10px) !important;
  background: rgba(255, 255, 255, 0.6) !important;
}
</style>